<template>
  <div class="device-group" :class="{'is-mobile': isMobile}">
    <div
      class="device-menu"
      :class="{
        'open-menu': menuOnMobile,
        'classic': BaseLayout.styleType === 'classic',
        'business': BaseLayout.styleType === 'business',
        'simplicity': BaseLayout.styleType === 'simplicity'
      }"
      ref="menuBox"
    >
      <el-tree :data="menuList" :props="defaultProps" @node-click="handleMenuClick"></el-tree>
    </div>
    <div class="device-content">
      <div class="ctrl-button">
        <div class="current-menu-seleced" ref="menuOpenButton">
          <span @click="showMenuList">
            <i class="el-icon-back"></i>
            {{selectedMenu}}
          </span>
        </div>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
        <el-dropdown trigger="click">
          <span class="el-dropdown-link">
            
            <i class="el-icon-arrow-down el-icon--right"></i>
          </span>
          <el-dropdown-menu slot="dropdown">
            <el-dropdown-item></el-dropdown-item>
          </el-dropdown-menu>
        </el-dropdown>
      </div>
      <el-tabs v-model="activeName" @tab-click="handleTabClick">
        <el-tab-pane label="" name="home">
          <Home></Home>
        </el-tab-pane>
        <el-tab-pane label="" name="config">
          <Config></Config>
        </el-tab-pane>
        <el-tab-pane label="" name="server">
          <ServerList></ServerList>
        </el-tab-pane>
        <el-tab-pane label="" name="loginfo">
          <LogsInfo></LogsInfo>
        </el-tab-pane>
        <el-tab-pane label="" name="event">
          <EventList></EventList>
        </el-tab-pane>
      </el-tabs>
    </div>
  </div>
</template>
<script>
import Home from "./DeviceGroup/Home";
import Config from "./DeviceGroup/Config";
import ServerList from "./DeviceGroup/ServerList";
import LogsInfo from "./DeviceGroup/LogsInfo";
import EventList from "./DeviceGroup/EventList";

import { domIsChild } from "@/utils";

export default {
  name: "DeviceGroup",
  components: { Home, Config, ServerList, LogsInfo, EventList },
  props: {},
  inject: ["screenType", "BaseLayout"],
  data() {
    return {
      activeName: "home",
      selectedMenu: "",
      menuList: [
        {
          label: "",
          level: 1,
          children: [
            { label: "" },
            { label: "DHCP" },
            { label: "" },
            { label: "1" },
            { label: "" },
            { label: "" },
            { label: "" },
            { label: "" },
            {
              label: "",
              children: [
                { label: "" },
                { label: "1" },
                { label: "2" },
                { label: "3" },
                { label: "4" }
              ]
            },
            {
              label: "",
              level: 1,
              children: [
                { label: "" },
                { label: "XZ-7F-1" },
                { label: "XZ-7F-2" }
              ]
            },
            {
              label: "",
              level: 1,
              children: [
                { label: "NSGY-7F-1" },
                { label: "NSGY-7F-2" },
                { label: "NSGY-7F-3" },
                { label: "NSGY-7F-4" },
                { label: "" }
              ]
            },
            {
              label: "",
              level: 1,
              children: [
                { label: "AC" },
                { label: "2F" },
                { label: "4F" },
                { label: "4F" },
                { label: "7F" },
                { label: "1#" },
                { label: "2#" },
                { label: "3#" },
                { label: "4#" }
              ]
            },
            {
              label: "3#",
              level: 1,
              children: [
                { label: "3#1" },
                { label: "3#2" },
                { label: "3#" }
              ]
            },
            {
              label: "",
              level: 1,
              children: [
                { label: "" },
                { label: "" },
                { label: "7FA" },
                { label: "5FA" },
                { label: "2FA" }
              ]
            },
            {
              label: "4#",
              level: 1,
              children: [
                { label: "4#" },
                { label: "4#1" },
                { label: "4#1" }
              ]
            },
            {
              label: "2#",
              level: 1,
              children: [{ label: "" }]
            },
            {
              label: "1#",
              level: 1,
              children: [{ label: "1#" }]
            },
            {
              label: "",
              level: 1,
              children: [{ label: "" }]
            },
            {
              label: "",
              level: 1,
              children: [{ label: "" }]
            }
          ]
        }
      ],
      defaultProps: {
        children: "children",
        label: "label"
      },
      menuOnMobile: false
    };
  },
  computed: {
    isMobile() {
      return this.screenType === "mobile";
    }
  },
  created() {},
  mounted() {
    document.documentElement.addEventListener("click", this.closeMenuList);
  },
  destroyed() {
    document.documentElement.removeEventListener("click", this.closeMenuList);
  },
  methods: {
    handleMenuClick(data) {
      if (data.level == 1) {
        return;
      }
      this.selectedMenu = data.label;
      this.menuOnMobile = false;
    },

    handleTabClick(data) {
      console.log(this.activeName);
    },

    showMenuList() {
      this.menuOnMobile = true;
    },

    closeMenuList() {
      const e = event || window.event;
      const ele = e.srcElement;
      const menuOpenButton = this.$refs.menuOpenButton;
      const menuBox = this.$refs.menuBox;
      if (!domIsChild(ele, menuOpenButton) && !domIsChild(ele, menuBox)) {
        this.menuOnMobile = false;
      }
    }
  }
};
</script>
<style lang="less" scoped>
.device-group {
  display: flex;
  min-height: 100%;
  .device-menu {
    min-width: 200px;
    width: 200px;
    border: 1px solid #0490d0;
  }
  .device-menu.classic {
    border: 1px solid #0490d0;
  }
  .device-menu.business {
    border: 1px solid #0490d0;
  }
  .device-menu.simplicity {
    border: 1px solid #bdd9f1;
  }
  .device-content {
    position: relative;
    flex: 10;
    padding: 0 20px;
    overflow: hidden;
    .ctrl-button {
      position: absolute;
      right: 30px;
      top: 0;
      line-height: 40px;
      z-index: 5;
      .current-menu-seleced {
        display: none;
      }
      .el-dropdown {
        margin-left: 15px;
        cursor: pointer;
        // color: white;
      }
    }
  }
}
.device-group.is-mobile {
  .device-menu {
    position: fixed;
    z-index: 100;
    top: 40px;
    bottom: 0;
    left: 0;
    width: 0px;
    min-width: 0px;
    background-color: #153c66;
    overflow-y: auto;
    transition: 0.5s all ease;
  }
  .device-menu.classic {
    background-color: #153c66;
  }
  .device-menu.business {
    background-color: #153c66;
  }
  .device-menu.simplicity {
    background-color: #3c769e;
  }
  .device-menu.open-menu {
    min-width: 200px;
    width: 200px;
  }
  .device-content {
    width: 100%;
    .ctrl-button {
      position: static;
      display: flex;
      border-bottom: 1px solid #ffffff;
      .current-menu-seleced {
        display: block;
        flex: 10;
        span {
          line-height: 40px;
          font-size: 14px;
          color: #05c6d6;
          i {
            font-size: 14px;
            color: #05c6d6;
          }
        }
      }
      .el-dropdown {
        flex: 1;
        min-width: 70px;
      }
    }
  }
}
</style>
